import React from 'react'
import {BrowserRouter as Router,Route,NavLink,Switch,Redirect} from 'react-router-dom'
import Taobao from './pages/Taobao'
import Cart from './taobao/Cart'
import Commodity from './taobao/Commodity'
import Dingdan from './taobao/Dingdan'
import Home from './taobao/Home'
import Mine from './taobao/Mine'
import Search from './taobao/Search'

const App1 = () => {
    return<Router>
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route path='/cart' component={Cart}/>
      <Route path='/dingdan' component={Dingdan}/>
      <Route path='/mine' component={Mine}/>
      <Route path='/home/search' component={Search}/>
      <Route path='/home/commodity' component={Commodity} />
      <Route render={()=><Redirect to='/' />} />
    </Switch>
    <Taobao />
  </Router> 
}

export default App1;
