import React from 'react'
import { Router } from 'react-router-dom'
import './Commodity.css'

const Commodity = (props) => {
    return (
        <div>
            <div className='com-top'>
                <div className='back' onClick={() => (props.history.push('/'))}>
                    <i style={{fontSize:21}} className='iconfont icon-fanhui'></i>
                </div>
                <img className='shangpin' src='https://img.alicdn.com/imgextra/i4/2206913270003/O1CN01nBJMJA1BtPotGhx5v_!!2206913270003-0-scmitem6000.jpg_760x760Q50s50.jpg_.webp'/>
            </div>


            <div className='com-bot'>
                <div className='shop'>
                    <i style={{fontSize:25}} className='iconfont icon-dianpu'></i>
                    <span>店铺</span>
                </div>
                <div className='wangwang'>
                    <i style={{fontSize:25}} className='iconfont icon-aliwangwang'></i>
                    <span>客服</span>
                </div>
                <div className='fav'>
                    <i style={{fontSize:25}} className='iconfont icon-shoucang'></i>
                    <span>收藏</span>
                </div>


                <div className='bot-left'>
                    <span>加入购物车</span>
                </div>
                <div className='bot-right'>
                    <span>立即购买</span>
                </div>
            </div>
        </div>
    )
}

export default Commodity
