import { Carousel } from 'antd-mobile';
import React from 'react'

class Lunbo extends React.Component {
  state = {
    data: ['1', '2', '3','4'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000003172/O1CN01ZWE1x71ZIowtKsQod_!!6000000003172-0-octopus.jpg','https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg'],
      });
    }, 100);
  }
  render() {
    return (
        <Carousel
          autoplay
          infinite
          // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          // afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.taobao.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}

export default Lunbo