const login = (username) =>{
    let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
    return (dispatch)=>{
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            dispatch({
                type: 'LOGIN',
                username: res.data.name
            })
        })
    }
    return {
        type:"LOGIN",
        username:username
    }
}


export {login}